.searchBar {
    display: grid;
    grid-template-columns: 5fr 1fr;

    input {
        all: unset;
        background-color: rgb(245, 245, 245);
        height: 1rem;
        padding: 1rem;
        margin-right: 1rem;
        border-radius: 0.5rem;
    }

    button {
        all: unset;
        background-color: rgb(0, 216, 0);
        padding-inline: 1rem;
        text-align: center;
        color: white;
        height: 3rem;
        margin-right: 1rem;
        border-radius: 0.5rem;
        width: fit-container(100%);
        box-shadow: none;
    }
}